import React, { Component } from 'react';
import Card from './components/Card'
import Intro from './components/Intro'
import Darah from './img/Darah.jpg'
import axios from 'axios';
class Blog extends Component {

    componentDidMount() {
        axios.get('https://script.googleusercontent.com/macros/echo?user_content_key=Ch3l5HNiH5_CDScDRi_Ykb2Dn2Hxy7jh6nERrBozIk4ECZxR32ZVKKpzMHngMvZYSghfn1Idn4ZLkjn7o1XmPJj-Exi16U97OJmA1Yb3SEsKFZqtv3DaNYcMrmhZHmUMWojr9NvTBuB6lHT6qnqYcmFWggwoSVQQXTsQ1HqKa1CgDXQROm1OeNR5ibYVAaRxAeOtzLmbRZcVjrce7Uveb8iU1s-L39A_vQK8SSXHAAREEMoiQ31HA2S2RLK1Djh8MGUS4MhBQRZ5dO17jneK-A4MAdorMS0Z&lib=M-tpZm-Fm1QX9Yr80nZn_p-WXe3zpGnIr').then(response => {
            this.setState({ members: response.data.data })
        })
            .catch(error => {
                console.log('error', error)
            })
    }
    constructor(props) {
        super(props)
        this.state = {
            members: []
        }
    }
    render() {
        return (

            <div className="container">
                <div className="row">
                    <div class="card">
                        <div class="card-body center">
                            <h1>Cek Stok Darah</h1>
                            <div className="card  col-md-6">
                                <img src={Darah} className="card-img" alt='' />
                                <div className="card-body text-justify">
                                    <h5 className="card-title">Kebutuhan Darah</h5>
                                    <p>PMI terus mengampanyekan donor darah sebagai bagian dari gaya hidup (lifestyle). Setiap tahunnya, PMI menargetkan hingga 4,5 juta kantong darah sesuai dengan kebutuhan darah nasional, disesuaikan dengan standar Lembaga Kesehatan Internasional (WHO) yaitu 2% dari jumlah penduduk untuk setiap harinya. Berikut data persediaan darah di beberapa Provinsi di Indonesia </p>

                                </div>
                            </div>
                            <h5 className="card-title">Dafar Darah </h5>
                            {this.state.members.map((member) => (
                                <div className="card text-justify col-md-12" key={member.id}>
                                    <div className="card-body">

                                        <h6 className="card-title">ID: {member.id}</h6>
                                        <h6 className="card-title">Unit: {member.unit}</h6>
                                        <h6 className="card-title">Provinsi: {member.provinsi}</h6>
                                        <h6 className="card-title">Jumlah: {member.jumlah}</h6>
                                    </div>

                                </div>


                            ))}

                        </div>
                    </div>

                </div>

            </div>

        )
    }





}

const styles = {
    box450border: {
        height: '450px',
        border: '2px solid #f1f1f1',
        borderRadius: 10,
        padding: 30
    },
}

export default Blog