import React from 'react';
import Card from './components/Card'
import Intro from './components/Intro'
import Tiga from './img/Tiga.jpg'
import Satu from './img/Satu.jpg'
import Dua from './img/Dua.jpg'

const intro ={
    content : 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industrys standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.',
}

const cards =[
    {title: 'Menjadi Programmer', imageUrl :'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-weekend.jpg', subtitle: 'It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout'},
    {title: 'Belajar Pemrograman', imageUrl :'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-intensive.jpg', subtitle: 'It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout'},
]



const Devschool =()=>(
    <div className="container">
        <h1 style={{textAlign:'center'}}>Developer School</h1>
        <div className="row" style={{marginTop: 40, marginBottom:100}}>
            <Intro content={intro.content} />
            <div className="col-md-6" >

                <img src='https://devschool.id/resources/themes/default/assets/img/foto-devschool.jpg' alt="Tiga"/>
                             

            </div>

        </div>

        <div className="row" style={{marginBottom:50}}>
            {
                cards.map(card=><Card imageUrl={card.imageUrl} title={card.title} subtitle={card.subtitle}/>)
            }

        </div>

    </div>
)
export default Devschool