import React, { Component } from 'react';
import axios from 'axios';

class Regitration extends Component {
    constructor(props) {
        super(props)
        this.state = {
            members: [],
            first_name: '', last_name: '', email: '',
        }
    }



    render() {
        return (


            <div className="container">
                <div className="row">

              
                <div className="col-md-6">
                    
                        <form onSubmit={this.onSubmitHandle}>
                            <div className="form-group">
                                <label >First Name</label>
                                <input type="text" className="form-control" name="first_name" value={this.state.first_name} onChange={this.inputOnChangeHandler} />
                            </div>
                            <div className="form-group">
                                <label>Last Name</label>
                                <input type="text" className="form-control" name="last_name" value={this.state.last_name} onChange={this.inputOnChangeHandler} />
                            </div>
                            <div className="form-group">
                                <label>Email</label>
                                <input type="email" className="form-control" name="email" value={this.state.email} onChange={this.inputOnChangeHandler} />
                            </div>
                            <button type="submit" className="btn btn-primary" >Submit</button>
                        </form>

                    
                </div>
                <div className="col-md-6">
                    {this.state.members.map((member) => (
                        <div  key={member.id}>
                            <div className="card" style={{ margin: 10 }}>
                                <div className="card-body" >
                                    <h5 className="card-title">ID Member :{member.id}</h5>
                                    <h5 className="card-title">First Name :{member.first_name} </h5>
                                    <h5 className="card-title">Last Name : {member.last_name}</h5>
                                    <h5 className="card-title">Email : {member.email}</h5>
                                    <h5 className="card-title">Register</h5>

                                </div>
                            </div>
                        </div>
                    )



                    )}
                </div>
                </div>
            </div>







        )

    }

    inputOnChangeHandler = (event) => {
        this.setState({ [event.target.name]: event.target.value })
    }
    onSubmitHandle = (e) => {

        e.preventDefault()
        var payload = {
            first_name: this.state.first_name,
            last_name: this.state.last_name,
            email: this.state.email
        }

        var url = 'https://reqres.in/api/users?page=1'
        axios.post(url, payload).then(response => {
            var members = [...this.state.members]
            members.push(response.data)
            this.setState({ members })

        })
            .catch(error => {
                console.log(error)
            })
    }


}





export default Regitration